import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { StorageService } from '../storage-service.service';
import { Observable } from 'rxjs';
import { API_CONFIG } from 'src/app/config/api.config';

@Injectable({
  providedIn: 'root'
})
export class NotifyService {

  constructor(private http: HttpClient, private storage: StorageService) { }

  requireInvitation(idJogador: string, idMestre?: string, mestreEmail?: string, idMesa?: string): Observable<any> {
    const params = new HttpParams()
      .set('idJogador', idJogador)
      .set('idMestre', idMestre)
      .set('mestreEmail', mestreEmail)
      .set('idMesa', idMesa);

    return this.http.get(`${API_CONFIG.baseUrl}notify/invitation`,
      {
        params,
        observe: 'body',
      });
  }

  contact(dto): Observable<any> {
    dto.email = this.storage.getLocalUser().email;
    console.log(dto);
    return this.http.post(`${API_CONFIG.baseUrl}notify/contatoJogador`,
      dto,
      {
        responseType: 'json',
        observe: 'body'
      })
  }
}
