import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { RpgService } from 'src/app/services/rpg/rpg.service';
import { PaginacaoService } from 'src/app/services/utils/paginacao.service';
import { MessageService } from 'primeng/api';
import { MesasService } from 'src/app/services/mesas/mesas.service';
import { MesaNewDTO } from 'src/app/models/Mesa/mesa-new.dto';
import { StorageService } from 'src/app/services/storage-service.service';
import { ModalController } from '@ionic/angular';

@Component({
  selector: 'app-nova-mesa',
  templateUrl: './nova-mesa.component.html',
  styleUrls: ['./nova-mesa.component.scss'],
})
export class NovaMesaComponent implements OnInit {

  @Output('obj')
  obj: EventEmitter<any> = new EventEmitter();

  paginaAtual = 0;
  tipoRPG: string = '0';
  flag: boolean = false;
  cancelOp: boolean = true;
  idRpg: string;

  constructor(private rpgService: RpgService,
    private mesaService: MesasService,
    private paginaService: PaginacaoService,
    private toastService: MessageService,
    private storageService: StorageService,
    private modalController: ModalController) { }

  ngOnInit() {
    //this.consultar();
  }
  getSelectedObj(evt) {
    this.obj.emit(evt);
    this.idRpg = evt.id;
    this.confirmar();
  }

  getNotification(evt) {
    if (this.paginaAtual != evt) {
      this.paginaAtual = evt;
      this.consultar();
    }
  }

  consultar() {
    this.flag = true;
    const p = this.rpgService.findPageByTipoRPG(this.tipoRPG, this.paginaAtual.toString())
    this.paginaService.setPromisse(p);
  }

  atualiza(event) {
    this.flag = false;
    this.tipoRPG = event.detail.value
  }

  confirmar() {
    this.toastService.clear();
    this.toastService.add({ key: 'c', sticky: true, severity: 'warn', summary: 'Are you sure?', detail: 'Confirm to proceed' });
  }

  onConfirm() {
    const dto: MesaNewDTO = {
      idMestre: this.storageService.getLocalUserId(),
      idSistema: this.idRpg
    };

    this.mesaService.insert(dto)
      .subscribe(res => {
        const { idMesa } = res;
        this.callToast('success', 'Mesa criada com sucesso!', 'Mesa: ' + idMesa);
        this.modalController.dismiss(true);
      },
        error => {
        });
  }

  onReject() {
    this.toastService.clear();
  }

  callToast(severity, summary, detail) {
    this.toastService.add({
      severity: severity,
      summary: summary,
      detail: detail
    });
  }

  close() {
    this.modalController.dismiss(false);
  }


}
