import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { NotifyService } from 'src/app/services/notify/notify.service';
import { StorageService } from 'src/app/services/storage-service.service';
import { MessageService } from 'primeng/api';

@Component({
  selector: 'app-invitation-form',
  templateUrl: './invitation-form.component.html',
  styleUrls: ['./invitation-form.component.scss'],
})
export class InvitationFormComponent implements OnInit {

  display = { show: false, titulo: 'Title', status: '' };
  opcao: number = 0;
  value: string;

  loading: boolean = false;

  @Output('notifyParent')
  notifyParent: EventEmitter<any> = new EventEmitter();
  closed(msg?) {
    if (msg) {
      this.notifyParent.emit('sucesso');
    } else {
      this.notifyParent.emit('closed');
    }

  }

  constructor(private notify: NotifyService, private storage: StorageService,
    private toastService: MessageService) { }

  ngOnInit() {
    this.showDialog("Insira uma das opÃ§Ãµes abaixo", "Email do mestre, cÃ³digo do mestre ou cÃ³digo da mesa");

  }

  showDialog(titulo: string, status: string) {
    this.display = {
      show: true,
      titulo,
      status
    }
  }

  cancel() {
    this.display = {
      show: false,
      status: '',
      titulo: ''
    }
    this.closed();
  }

  send() {
    this.display = {
      show: false,
      status: '',
      titulo: ''
    }

    this.loading = true;
    let idMestre, emailMestre, idMesa;

    switch (this.opcao) {
      case 0:
        idMestre = this.value;
        break;

      case 1:
        emailMestre = this.value;
        break;

      case 2:
        idMesa = this.value;
        break;
    }

    this.notify.requireInvitation(this.storage.getLocalUserId(), idMestre, emailMestre, idMesa)
      .subscribe(res => {
        this.loading = false;
        this.closed(true);
      },
        error => {
          this.loading = false;
          this.closed();
        })
  }



}
