import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';

import { PaginacaoModule } from '../utils/paginacao/paginacao.module';
import { LoadSpinnerModule } from '../utils/load-spinner/load-spinner.module';
import { NovaMesaComponent } from './nova-mesa/nova-mesa.component';
import { MessageService } from 'primeng/api';
import { ToastModule } from 'primeng/toast';
import { ButtonModule } from 'primeng/button';




@NgModule({
  declarations: [NovaMesaComponent],
  imports: [
    CommonModule,
    IonicModule,
    FormsModule,
    ReactiveFormsModule,
    LoadSpinnerModule,
    PaginacaoModule,
    ToastModule,
    ButtonModule
  ],
  entryComponents: [NovaMesaComponent],
  exports: [NovaMesaComponent],
  providers: [
    MessageService
  ]
})
export class NovaMesaModule { }
